import "./styles.css";
import React, { useState, useEffect, useRef } from "react";
import Routing from "./Routing";
import { useDispatch } from "react-redux";
import { setStory } from "./store/story/Actions";
import { PROLOG, DAY1, MONIKA } from "./plots/stores";

export default function App() {
  const [audioBackground, setAudioBackground] = useState(null);
  const audio = useRef(new Audio(audioBackground));

  const dispatch = useDispatch();

  useEffect(() => {
    if (audio.current != null && audioBackground != null) {
      audio.current.src = audioBackground;
      audio.current.pause();
      audio.current.currentTime = 0;
      audio.current.play();
      audio.current.loop = true;
    } else if (audioBackground == null) {
      try {
        audio.current.pause();
        audio.current.currentTime = 0;
      } catch {}
    }

    dispatch(setStory("PROLOG", PROLOG));
    dispatch(setStory("MONIKA", MONIKA));
    dispatch(setStory("DAY1", DAY1));
  }, [audioBackground]);

  return <Routing setAudioBackground={setAudioBackground} />;
}
