import React, { useState } from "react";
import styles from "./styles/Chat.module.css";

export default function Chat(props) {
  const [i, setI] = useState(0);
  const [clickable, setClickable] = useState(true);
  const [messages, setMessages] = useState([]);
  const [kb, setKb] = useState([]);

  let monikaStickers = [
    "https://i.ibb.co/whbvXGW/3dc3d73f-53a8-4040-9bf7-619b66b0a738.png",
    "https://i.ibb.co/znjwcKd/Monika-Sticker-Excited.png"
  ];

  let plot = [
    {
      text: "ddsa",
      from: "self-message"
    },
    {
      text: "djaksdj",
      from: "self-message"
    },
    {
      text: "adsasd",
      from: "self-message"
    },
    {
      text: "ddd",
      sticker: monikaStickers[1]
    },
    {
      image: "https://i.ibb.co/Ctdkh7R/474483da-0803-4ec6-afcf-5c8565405907.jpg"
    },
    {
      image:
        "https://genshin.global/wp-content/uploads/2022/10/anniversary-nahida-venti-raiden-zhongli-official-desktop-wallpaper-genshin.jpg",
      keyboard: ["Привет.", "Как дела?", "Чем занимаешься?", "Давай сыграем."]
    }
  ];

  let plots = {
    "Привет.": [
      {
        text: "qqq kdljas dkljas dkljjas dljasj dkljlasjd ljalsdj lj"
      },
      {
        text: "eee",
        keyboard: ["Как дела?"]
      }
    ],
    "Как дела?": [
      {
        text: "Все нормально"
      },
      {
        text: "А у тебя как?"
      }
    ]
  };

  function renderMessages() {
    return messages.map((data, index) => {
      if (data.from === "self-message") {
        return (
          <div key={index} className={styles.selfMessage}>
            {data.text}
          </div>
        );
      } else if (data.from === "bot-message" || data.from === undefined) {
        return (
          <div key={index} className={styles.botMessage}>
            <img
              className={styles.sticker}
              src={data?.sticker != null ? data.sticker : monikaStickers[0]}
              alt=""
            />
            <span>{data?.text !== undefined ? data.text : ""}</span>
            {data?.image !== undefined ? (
              <img src={data?.image} className={styles.messageImg} alt="" />
            ) : (
              ""
            )}
          </div>
        );
      }
    });
  }

  function addMessageButton(kButton) {
    setClickable(true);
    setMessages((old) => {
      return [...old, { from: "self-message", text: kButton }];
    });
    setKb([]);
    plot.push(...plots[kButton]);
    console.log(plot);
  }

  function renderKeyboard() {
    if (kb) {
      return kb.map((kButton) => {
        return (
          <div
            onClick={() => addMessageButton(kButton)}
            key={kButton}
            className={styles.key}
          >
            {kButton}
          </div>
        );
      });
    }
  }

  function clicked() {
    if (clickable) {
      setMessages((old) => [...old, plot[i]]);

      console.log(plot[i]);
      if (plot[i].keyboard !== undefined) {
        setClickable(true);
      } else {
        setClickable(false);
        setKb(plot[i].keyboard);
      }
    }
    setI(i + 1);
  }

  return (
    <div className={styles.chat}>
      <div className={styles.messenger} onClick={clicked}>
        <div className={styles.keyboard}>{renderKeyboard()}</div>
        <div className={styles.messages}>{renderMessages()}</div>
      </div>
    </div>
  );
}
