import React, { useState, useEffect } from "react";
import styles from "../styles/FileManager.module.css";

export default function Dirs(props) {
  const [catalogs, setCatalogs] = useState(props.files.default);

  useEffect(() => {
    if (props.currentDir === "/default") {
      setCatalogs(props.files.default);
    } else if (props.currentDir === "/files") {
      setCatalogs(props.files.files);
    } else if (props.currentDir === "/music") {
      setCatalogs(props.files.music);
    } else if (props.currentDir === "/images") {
      setCatalogs(props.files.images);
    } else if (props.currentDir === "/m$0n~Ik@") {
      setCatalogs(props.files.monika);
    }
  }, [props.currentDir]);

  function goTo(item) {
    if (item.type === "folder") {
      props.setCurrentDir(item.path);
    }
  }

  return (
    <div className={styles.dirs}>
      {catalogs.map((item) => (
        <div
          className={styles.item}
          key={item.title}
          onClick={() => goTo(item)}
        >
          <img
            className={styles.itemIco}
            src={
              item.type === "folder"
                ? props.assets.folder
                : item.type === "musicFile"
                ? props.assets.musicFile
                : item.type === "textFile"
                ? props.assets.textFile
                : item.type === "imageFile"
                ? props.assets.imageFile
                : ""
            }
            alt=""
          />
          {item.title}
        </div>
      ))}
    </div>
  );
}
